import React from 'react';

export default class SignUpForm extends React.Component {
    render () {
        return (
            <form className="signUp" onSubmit={this.props.onSubmit.bind(this)}>
                <div className="row">
                    <label></label>
                    <input type="text" value={this.props.formData.username} onChange={this.props.onChange.bind(this,'username')}/>
                </div>
                <div className="row">
                    <label></label>
                    <input type="password" value={this.props.formData.password} onChange={this.props.onChange.bind(this,'password')}/>
                </div>
                <div className="row">
                    <label></label>
                    <input type="email" value={this.props.formData.email} onChange={this.props.onChange.bind(this,'email')}/>
                </div>
                <div className="row actions">
                    <button className="submit"></button>
                </div>
            </form>
        );
    }
}


