import React from 'react';
import './UserDialog.css'
import {signUp,signIn,sendPasswordResetEmail} from './leanCloud'
import ForgotPassword from './ForgotPassword'
import SignUpOrSignIn from './SignUpOrSignIn'

export default class UserDialog extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selected: 'signUp',
            selectedTab: 'signUpOrSignIn', //
            formData: {
                username: '',
                password: '',
                email: ''
            }
        }
    }
    //selected

    signUp(e){
        e.preventDefault()
        let {username, password, email} = this.state.formData
        let successFn = (user)=>{
            this.props.onSignUp(user);
        }
        let errorFn = (error)=>{
            switch (error.code){
                case 202:
                    alert('');
                    break;
                case 124:
                    alert('');
                    break;
                case 126:
                    alert(' Id');
                    break;
                case 139:
                    alert('');
                    break;
                case 200:
                    alert('');
                    break;
                case 201:
                    alert('');
                    break;
                case 217:
                    alert('');
                    break;
                case 218:
                    alert('');
                    break;
                default:
                    alert(error);
                    break;
            }
        }
        signUp(username, password, email, successFn, errorFn)
    }
    signIn(e){
        e.preventDefault()
        let {username, password} = this.state.formData
        let successFn = (user)=>{
            this.props.onSignIn(user);
        }
        let errorFn = (error)=>{
            switch (error.code) {
                case 210:
                    alert('');
                    break;
                case 139:
                    alert('');
                    break;
                case 200:
                    alert('');
                    break;
                case 201:
                    alert('');
                    break;
                case 210:
                    alert('');
                    break;
                case 211:
                    alert('');
                    break;
                case 219:
                    alert('15');
                    break;
                default:
                    alert(error);
                    break;
            }
        }
        signIn(username, password, successFn, errorFn)
    }
    changeFormDate(key,e){
        let stateCopy = JSON.parse(JSON.stringify(this.state)); //this.state,setState,this.statewarning
        stateCopy.formData[key] = e.target.value;
        this.setState(stateCopy);
    }
    showForgetPassword() {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.selectedTab = 'forgotPassword';
        this.setState(stateCopy);
    }
    resetPassword(e) {
        e.preventDefault();
        sendPasswordResetEmail(this.state.formData.email);
    }
    returnToSignIn() {
        let stateCopy = JSON.parse(JSON.stringify(this.state));
        stateCopy.selectedTab = 'signUpOrSignIn';
        this.setState(stateCopy);
    }
    //render
    render () {
        return (
            <div className="UserDialog-Wrapper">
                <div className="UserDialog">
                    {this.state.selectedTab === 'signUpOrSignIn' ?
                        <SignUpOrSignIn
                            formData={this.state.formData}
                            onSignUp={this.signUp.bind(this)}
                            onSignIn={this.signIn.bind(this)}
                            onChange={this.changeFormDate.bind(this)}
                            onForgotPassword={this.showForgetPassword.bind(this)}>
                        </SignUpOrSignIn> :
                        <ForgotPassword
                            formData={this.state.formData}
                            onSubmit={this.resetPassword.bind(this)}
                            onChange={this.changeFormDate.bind(this)}
                            onReturn={this.returnToSignIn.bind(this)}>
                        </ForgotPassword>}
                </div>
            </div>
        )
    }
}


