import React from 'react';
import SignUpForm from './SignUpForm'
import SignInForm from './SignInForm'

export default class SignUpOrSignIn extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selected: 'signUp'
        }
    }
    switch(e) {
        this.setState({
            selected: e.target.value,
            selectedTab: 'signUpOrSignIn'
        })
    }
    render () {
        return (
            <div className="onSignUpOrSignIn">
                <nav>
                    <label><input type="radio" value='signUp' checked={this.state.selected === 'signUp'} onChange={this.switch.bind(this)}/></label>
                    <label><input type="radio" value="signIn" checked={this.state.selected === 'signIn'} onChange={this.switch.bind(this)}/></label>
                </nav>

                <div className="panes">
                    {/*navsignUpreturn signUpForm,signIn,return signInForm*/}
                    {this.state.selected === 'signUp' ? <SignUpForm formData={this.props.formData}
                                                                    onSubmit={this.props.onSignUp}
                                                                    onChange={this.props.onChange}>
                    </SignUpForm> : null}
                    {this.state.selected === 'signIn' ? <SignInForm formData={this.props.formData}
                                                                    onSubmit={this.props.onSignIn}
                                                                    onChange={this.props.onChange}
                                                                    onForgotPassword={this.props.onForgotPassword}>
                    </SignInForm> : null}
                </div>
            </div>
        );
    }
}


